/*  */
html {
  font-size: 100px;
}
body {
  font-family: "PingFang SC", "San Francisco Display", "Source Han Sans CN", "Microsoft Yahei", "Heiti SC", Arial, "", "";
  font-size: 14px;
}
/*  */
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
fieldset,
lengend,
button,
input,
textarea,
th,
td {
  /* table elements  */
  margin: 0;
  padding: 0;
}
h1 {
  font-size: 18px;
  /* 18px / 12px = 1.5 */
}
h2 {
  font-size: 16px;
}
h3 {
  font-size: 14px;
}
h4,
h5,
h6 {
  font-size: 100%;
}
address,
cite,
dfn,
em,
var {
  font-style: normal;
}
/*  */
code,
kbd,
pre,
samp,
tt {
  font-family: "Courier New", Courier, monospace;
}
/*  */
small {
  font-size: 12px;
}
/*  12px  small  */
/*  */
ul,
ol {
  list-style: none;
}
/*  */
a {
  text-decoration: none!important;
}
a:hover {
  text-decoration: underline;
}
abbr[title],
acronym[title] {
  /* 1.ie6  abbr; 2.ie6  */
  border-bottom: 1px dotted;
  cursor: help;
}
/*  */
legend {
  color: #000;
}
/* for ie6 */
fieldset,
img {
  border: none;
}
/* img  img  */
/* optgroup  */
button,
input,
select,
textarea {
  font-size: 100%;
  /*  ie  */
}
/*  */
table {
  border-collapse: collapse;
  border-spacing: 0;
}
i {
  vertical-align: middle;
}
.editor-container .dialog-container .dialog {
  margin-top: 0!important;
  top: 5%;
  height: 500px!important;
}



